@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700&display=swap);
html, body, #root {
    height: 100%;
}
* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
    color: #1e1e23;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    min-width: 300px;
}

body {
    min-height: 100%;
    font-family: 'Montserrat', Helvetica, sans-serif;
}

h1, h2, h3, h4 {
    display: block;
    margin: 0;
}

.fp-tableCell {
    box-sizing: border-box;
    width: 100%;
    display: inherit !important;
    flex-flow: inherit;
    flex: inherit;
    justify-content: inherit;
    align-items: inherit;
}

.sticky-header {
    position: fixed;
    left: 20px;
    top: 20px;
    z-index: 3;
}

.sticky-header a, .sticky-header a:hover {
    color: #fff;
    text-decoration: none;
    text-transform: none;
}
.banner {
    background-image: url('/img/background-overlay.png');
    padding: 0 15px;
    background-size: cover;
    background-position: top center;
    z-index: 2;
}

.banner h1 {
    color: #fff;
    font-size: 64px;
    text-align: center;
}

.banner h2 {
    color: #fff;
    font-size: 24px;
    text-align: center;
}

.red {
    background-color: rgb(164, 35, 52);
    color: #fff;
}

.black {
    background-color: rgb(27, 27, 28);
    color: #fff;
}

.blue {
    background-color: rgb(2, 83, 253);
    color: #fff;
}

.lightgray {
    background-color: #f5f5f5;
    color: #636363;
}

.gray {
    background-color: #8f8f8f;
    color: #fff;
}

.darkgray {
    background-color: #636363;
    color: #fff;
}

.projects {
    min-height: 100%;
}

.half {
    width: 50%;
}

@media only screen and (min-width: 580px) {
    .banner h1 {
        color: #fff;
        font-size: 100px;
        text-align: center;
    }

    .banner h2 {
        color: #fff;
        font-size: 48px;
        text-align: center;
    }
}
.project {
    width: 100%;
    cursor: pointer;
    padding: 0 5px;
    position: relative;
}

.project h3 {
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    z-index: 2;
}

.project h4 {
    font-size: 12px;
    font-weight: 300;
    text-align: center;
    z-index: 2;
}

.project .thumb {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    opacity: 0;
}

.project:hover .thumb {
    -webkit-transition: opacity 7s;
    transition: opacity 7s;
    opacity: .5;
}
.case-study .header {
    background-size: cover;
}

.case-study .header .headline {
    padding: 20px;
    width: 100%;
    background: rgb(0,0,0);
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0.5)), to(rgba(0,0,0,0)));
    background: linear-gradient(0deg, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
}

.case-study .headline h1 {
    color: #fff;
    font-weight: 500;
    margin-bottom: 10px;
}

.case-study .headline h2 {
    color: #eee;
    font-weight: 300;
    font-size: 16px;
}

.case-study .case-section {
    position: relative;
}

.case-study .case-section .case-image {
    position: absolute;
    width: 48%;
    top: 0;
    left: 52%;
    height: 100%;
    overflow: hidden;
    background-color: transparent;
}

.case-study .case-section .image {
    width: 500px;
    height: 500px;
    background-size: cover;
    background-position: center left;
}

.case-study .case-section .info {
    padding: 0 0 0 50px;
}

.case-study .case-section h2 {
    font-size: 48px;
}

.case-study .case-section p {
    font-size: 24px;
    color: #636363;
    line-height: 40px;
}
